/** @format */

"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";

import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { formSchema } from "@/utils/schema";
import { Textarea } from "../ui/textarea";
import { DatePickerComponent } from "../elements/date-picker";
import { DropdownComponent } from "../elements/dropdown";
import { LucideFlag } from "lucide-react";
import { Divider } from "../elements/divider";

export function TaskForm() {
  // 1. Define your form.
  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      title: "",
      description: "",
      dueDate: undefined,
      priority: "low",
    },
  });

  const options = ["low", "medium", "high"];

  // 2. Define a submit handler.
  function onSubmit(values: z.infer<typeof formSchema>) {
    // Do something with the form values.
    //  This will be type-safe and validated.
    console.log(values);
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className='space-y-2'>
        <FormField
          control={form.control}
          name='title'
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Input
                  className='text-xl font-semibold placeholder:text-xl border-0 shadow-none focus-visible:ring-0 px-0'
                  placeholder='Cook rice with Debs'
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name='description'
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Textarea
                  className='border-0 shadow-none font-semibold focus-visible:ring-ring/0 px-0'
                  placeholder='Type your description here.'
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name='description'
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Textarea
                  className='border-0 shadow-none font-semibold focus-visible:ring-ring/0 px-0'
                  placeholder='Type your description here.'
                  {...field}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <Divider />
        <div className='flex items-center gap-2'>
          <FormField
            control={form.control}
            name='dueDate'
            render={({ field }) => (
              <FormItem>
                <FormControl>
                  <DatePickerComponent label={"due date"} {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='priority'
            render={({ field }) => (
              <FormItem>
                <FormControl>
                  <DropdownComponent
                    {...field}
                    label={
                      <span className='flex items-center'>
                        <LucideFlag />
                        Priority
                      </span>
                    }
                    options={options}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>
        <Button type='submit'>Create task</Button>
      </form>
    </Form>
  );
}
